<template>
    <div>
        <!-- <div class="head">
            <div class="backBtn"></div>
            <div class="title">我的收藏</div>
            <div class="status">编辑</div>
        </div> -->
        <div v-if="favoriteList==''" class="empty">
            <img src="https://pic.repaiapp.com/static/png/20180426/18/1524738352003615097.png">
        </div>
        <div class="favoriteList" v-if="favoriteList!=''">
            <div class="dateItem" v-for="(item,index) in favoriteList">
                <div class="dateTitle">{{item.time}}</div>
                <div class="goodContent" v-for="j in item.data">
                    <div class="goodImg"><img :src='j.thumb'/></div>
                    <div class="goodText">
                        <div class="goodTitle">{{j.title}}</div>
                        <div>
                            <span class="price">¥{{j.marketprice}}</span>
                            <span class="oldPrice">¥{{j.productprice}}</span>
                        </div>
                    </div>
                    <div class="delBtn" @click="channel(j.id)"></div>
                </div>
            </div>
        </div>
        <div class="sj-hint">加入成功</div>

    </div>
</template>

<script>
    import api from '../network/request'
    import { Toast } from 'vant'
    export default {
        name: "favorite",
        components: {

        },
        data(){
            return {
                favoriteList:''
            }
        },
        methods: {
            shop:function(){
                var _this=this;
                const params={access_token:localStorage.getItem('access_token')};
                api.post('user/favori_lists',params,res=>{
                console.log(res);
                    if(res.status==0){
                        _this.favoriteList=res.data.lists;
                        // _this.status=res.data;
                    }
                    if(res.status==600){
                        this.$router.push({name: 'login', params: {}})
                    }
                }) 
            },
            channel:function(id){
                var _this=this;
                const params={access_token:localStorage.getItem('access_token'),goods_ids:id,type:-1};
                api.post('user/favorite',params,res=>{
                console.log(res);
                    if(res.status==0){
                        Toast.success("删除成功")
                        // $(".sj-hint").html("取消成功").fadeIn().delay(1500).fadeOut();
                        this.shop();
                        // _this.favoriteList=res.data.lists;
                        // _this.status=res.data;
                    }
                    if(res.status==600){
                        this.$router.push({name: 'login', params: {}})
                    }
                })
            }

        },
        created() {
            this.shop();


        }
    }
</script>

<style scoped lang="scss">
    .empty{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 500px;
    }
    .empty img{
        width: 142px;
        height: 112px;
    }
    .head{
        height: 45px;
        line-height: 45px;
        background-color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        color: #000000;
        border-bottom: 1px #dadada solid;
        .backBtn{
            flex-basis: 45px;
            box-sizing: border-box;
            width: 10px;
            height: 16px;
            background: url("data:image/png;base64,") no-repeat;
            background-size: contain;
            background-position:10px;
        }
        .title{
            flex: 1;
            text-align: center;
        }
        .status{
            flex-basis: 45px;
            color: #dd3232;
            font-size: 15px;
        }
    }

    .favoriteList{
        position: relative;
        font-size: 13px;
        background-color: #fff;
        .dateItem{
            .dateTitle{
                padding-left: 15px;
                line-height: 35px;
                color:#838383;
                background-color: #f6f6f6;
            }
            .goodContent{
                display: flex;
                align-items:center;
                padding:17px;
                border-bottom: 1px solid #dadada;
                .goodImg{
                    img{
                        box-sizing: border-box;
                        width: 80px;
                        height: 80px;
                        border: 1px solid #bec2c9;
                    }
                }
                .goodText{
                    display: flex;
                    flex: 1;
                    flex-flow:column;
                    justify-content: space-between;
                    height: 75px;
                    padding-left:10px;
                    .goodTitle{
                        font-size: 14px;
                        font-weight: 600;
                        line-height: 18px;
                        color:#000;
                    }
                    .price{
                        font-size: 16px;
                        color:#ea4c3c
                    }
                    .oldPrice{
                        padding-left: 10px;
                        font-size: 11px;
                        color: #96989a;
                        text-decoration:line-through;
                    }
                }
                .delBtn{
                    width: 13px;
                    height: 16px;
                    background: url("data:image/png;base64,")  no-repeat;
                    background-size: contain;
                    align-self: flex-end;
                }
            }
            .goodContent:last-child{
                border-bottom: 0;
            }
        }
    }

</style>



