<template>
    <div>
        <!-- <div class="head">
            <div class="backBtn"></div>
            <div class="title">设置</div>
            <div class="status"></div>
        </div> -->
        <div class="content">
            <div class="user" @click="setInfo()">
                <img class="avatar" :src="avatar"/>
                <div class="userInfo">
                    <div class="userName">{{nickname}}</div>
                    <div class="userID">ID:{{id}}</div>
                </div>
                <div class="userIcon"></div>
            </div>
           <!--  <div class="picLevel">
                <div class="icon picIcon"></div>
                <div class="title">图片质量</div>
                <div class="picModel">{{picModel}}</div>
                <div class="userIcon"></div>
            </div> -->
            <div class="setItem" style="margin: 10px 0;" @click="myteam">
                <div class="icon teamIcon"></div>
                <div class="title">我的团队</div>
                <div class="userIcon"></div>
            </div>
            <div class="setItem" @click="intro">
                <div class="icon companyIcon"></div>
                <div class="title">企业简介</div>
                <div class="userIcon"></div>
            </div>
            <div class="setItem" @click="opinion()">
                <div class="icon adviceIcon"></div>
                <div class="title">意见反馈</div>
                <div class="userIcon"></div>
            </div>
           <!--  <div class="setItem">
                <div class="icon scoreIcon"></div>
                <div class="title">给香天下评分</div>
                <div class="userIcon"></div>
            </div> -->
        </div>
        <!-- <div class="logoutBtn" @click="exit">退出登录</div> -->
    </div>
</template>

<script>
    import api from '../network/request'
    import { Dialog } from 'vant';
    export default {
        name: "setting",
        components: {
            
        },
        data(){
            return {
                "user":{
                    "img":"https://pic.repaiapp.com/static/jpg/20180413/11/1523589580121399101.jpg",
                    "name":"bin",
                    "id":"bin123"
                },
                "picModel":"智能模式",
                avatar:'',
                nickname:'',
                id:''
            }
        },
        methods: {
            myteam(){
                this.$router.push({name: 'myTeam', params: {}})
            },
            setInfo:function(){
                this.$router.push({name: 'setInfo', params: {}})
            },
            //意见反馈
            opinion:function(){
                this.$router.push({name: 'opinion', params: {}})  
            },
            //企业介绍
            // https://dc.xiaodian.in/web/index.php?c=xiangtianxia&a=view&do=copyInfo
            intro:function(){
                window.location.href="https://dc.xiaodian.in/web/index.php?c=xiangtianxia&a=view&do=copyInfo"
            },
            exit:function(){
                Dialog.confirm({
                  title: '提示',
                  message: '确定要退出登录吗？'
                }).then(() => {
                    var _this=this;
                    const params={access_token:localStorage.getItem('access_token')};
                    api.post('user/logout',params,res=>{
                    console.log(res);
                        if(res.status==0){
                            localStorage.setItem('access_token','');
                            this.$router.push({name: 'index', params: {}})
                        }
                        if(res.status==600){
                            this.$router.push({name: 'login', params: {}})
                        }
                    })

                }).catch(() => {
                    this.restSlide();
                  // on cancel
                });

            }

        },
        created() {
            this.avatar=localStorage.getItem('avatar')
            this.nickname=localStorage.getItem('nickname')
            this.id=localStorage.getItem('useId')

        }
    }
</script>

<style scoped lang="scss">
    .head {
        height: 45px;
        line-height: 45px;
        background-color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        color: #000000;
        border-bottom: 1px #dadada solid;
        .backBtn {
            flex-basis: 45px;
            box-sizing: border-box;
            width: 10px;
            height: 16px;
            background: url("data:image/png;base64,") no-repeat;
            background-size: contain;
            background-position: 10px;
        }
        .title {
            flex: 1;
            text-align: center;
        }

        .status {
            flex-basis: 45px;
            color: #dd3232;
            font-size: 15px;
        }
    }
    .content{
        .user{
            display: flex;
            align-items: center;
            padding:14px;
            border-bottom: 1px solid #dadada;
            background:#fff;
            .avatar{
                width: 71px;
                height: 71px;
                margin-right: 12px;
                border-radius: 50%;
                border:1px solid #efeff4;
            }
            .userInfo{
                display: flex;
                flex:1;
                flex-flow: column;
                justify-content:center;
                .userName{
                    font-size: 16px;
                    font-weight: 600;
                    color:#262626;
                }
                .userID{
                    font-size: 15px;
                    color: #b9b9b9;
                }
            }
            .userIcon{
                flex-basis: 7px;
                width: 7px;
                height: 12px;
                background: url("data:image/png;base64,") no-repeat;
                background-size: contain;
            }
        }
        .picLevel{
            display:flex;
            align-items: center;
            padding: 18px 14px;
            margin:10px 0;
            border-top:1px solid #dadada;
            border-bottom:1px solid #dadada;
            font-size: 16px;
            color:#262626;
            background:#fff;
            .icon{
                width: 16px;
                height: 16px;
                margin-right: 10px;
                background-size: contain;
                background-position: 50%;
            }
            .title{
                flex:1;
            }
            .picModel{
                flex-basis: 60px;
                margin-right: 7px;
                font-size: 14px;
                color: #b9b9b9;
            }
            .userIcon{
                flex-basis: 7px;
                width: 7px;
                height: 12px;
                background: url("data:image/png;base64,") no-repeat;
                background-size: contain;
            }
        }
        .setItem{
            display:flex;
            align-items: center;
            padding: 18px 14px;
            border-top:1px solid #dadada;
            font-size: 16px;
            color:#262626;
            background:#fff;
            .icon{
                width: 16px;
                height: 16px;
                margin-right: 10px;
                background-size: contain;
                background-position: 50%;
            }
            .title{
                flex:1;
            }
            .userIcon{
                flex-basis: 7px;
                width: 7px;
                height: 12px;
                background: url("data:image/png;base64,") no-repeat;
                background-size: contain;
            }
        }
        .setItem:last-child{
            border-bottom:1px solid #dadada;
        }
    }
    .logoutBtn{
        margin: 40px auto;
        text-align: center;
        width: 345px;
        height:48px;
        border-radius: 3px;
        line-height: 48px;
        font-size: 17px;
        color:#fff;
        background:#d9372d ;
    }
    .picIcon{
        background: url("data:image/png;base64,") no-repeat;
    }
    .teamIcon{
        background: url("data:image/png;base64,") no-repeat;
    }
    .companyIcon{
        background: url("data:image/png;base64,") no-repeat;
    }
    .adviceIcon{
        background: url("data:image/png;base64,") no-repeat;
    }
    .scoreIcon{
        background: url("data:image/png;base64,") no-repeat;
    }


</style>



