<template>
    <div class="review">
        <!-- <div class="head">
            <div class="backBtn"></div>
            <div class="title">评价</div>
            <div class="status"></div>
        </div> -->
        <div class="content">
            <div class="overview">
                <img :src="goodImg">
                <div>
                    <p class="title">商品评分</p>
                    <star class="star" :size="28" :score="score" position="left" @select="starSelect"></star>
                </div>
            </div>
            <textarea placeholder="你的评论能帮助其他小伙伴哦" rows="7" ref="input1" style="box-sizing: border-box;"></textarea>
            <upload class="upLoad" size="small" @imgs="imgs" @delItem="delItem"></upload>
            <div class="hideBtn"><span :class="{active:hideHandle}" @click="hideBtn">匿名评论</span></div>
            <div class="btn" @click="comfirm()">提交</div>
        </div>
        <div class="sj-hint"></div>    
    </div>
</template>

<script>
    import star from 'components/star/star';
    import upload from 'components/upload/upload';
    import api from '../network/request'
    import { Toast } from 'vant'
    export default {
        name: "favorite",
        data(){
            return {
                "goodImg":"https://pic.repaiapp.com/static/png/20180412/16/1523520607370764956.png",
                "score":5,
                "picArr":[
                    "https://pic.repaiapp.com/static/png/20180417/11/1523935740194810257.png",
                    "https://pic.repaiapp.com/static/jpg/20180413/11/1523589580121399101.jpg",
                    "https://pic.repaiapp.com/static/png/20180412/16/1523520607370764956.png"
                ],
                "hideHandle":true,
                imgss:''
            }
        },
        methods: {
            imgs:function(e){
                console.log(e);
                this.imgs=e;
                this.imgss=e;
            },
            starSelect(index){
                this.score = index;
                console.log(index)
            },
            delItem(index) {
                this.imgs.splice(index,1)
            },
            hideBtn() {
                if(this.hideHandle === true) {
                    this.hideHandle = false
                } else {
                    this.hideHandle = true
                }
            },
            //提交
            comfirm:function(){
                var _this=this;
                var orderid=localStorage.getItem('myorderid');
                var level=this.score;
                var content=this.$refs.input1.value;
                var goodsid=this.$route.params.goodsid;
                if(this.imgs.length>0&&this.imgss!=''){
                    var images=_this.imgs.join(',')
                }else{
                    var images=_this.imgss;
                }
                if(this.hideHandle){
                    var anonymous=1
                }else{
                    var anonymous=0;
                }

                if(level==0){
                    $(".sj-hint").html("请选择评分").fadeIn().delay(1500).fadeOut();
                    return;
                }
                const params={access_token:localStorage.getItem("access_token"),orderid:orderid,goodsid:goodsid,level:level,content:content,images:images,anonymous:anonymous};
                api.post('order/comment',params,res=>{
                console.log(res);
                    if(res.status==0){
                        Toast.success("评论成功")
                        // $(".sj-hint").html("评论成功").fadeIn().delay(1500).fadeOut();
                       this.goback();
                       // this.$router.go(-1);
                    }
                    if(res.status==600){
                      this.$router.push({name: 'login', params: {}})
                    }
                })
                
            },
            goback:function(){
                var _this=this;
                setTimeout(function(){
                    _this.$router.go(-1);
                 },800)
                // Toast.clear();
            },
        },
        created() {
            this.goodImg=this.$route.params.img

        },
        components:{
            star,
            upload
        }
    }
</script>

<style scoped lang="scss">
    .review{
        .head{
            height: 45px;
            line-height: 45px;
            background-color: #fff;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 18px;
            color: #000000;
            border-bottom: 1px #dadada solid;
            .backBtn{
                flex-basis: 45px;
                box-sizing: border-box;
                width: 10px;
                height: 16px;
                background: url("data:image/png;base64,") no-repeat;
                background-size: contain;
                background-position:10px;
            }
            .title{
                flex: 1;
                text-align: center;
            }
            .status{
                flex-basis: 45px;
                color: #dd3232;
                font-size: 15px;
            }
        }
         .content {
             background: #fff;
             .overview{
                 display: flex;
                 padding:15px;
                 img{
                     flex-basis: 75px;
                     width: 75px;
                     height: 75px;
                 }
                 div{
                     flex: 1;
                     margin-top:10px;
                     margin-left: 20px;
                     .title{
                         font-size: 16px;
                         color: #333;
                     }
                 }
             }
             textarea{
                 resize:none;
                 outline:none;
                 width: 100%;
                 padding: 10px;
                 border:0;
                 font-size: 14px;
                 background: #f6f6f6;
             }
             .upLoad{
                 padding: 10px 10px;
             }
             .hideBtn{
                 padding: 13px;
                 border-top: 1px solid #d3d3d3;
                 font-size: 14px;
                 color: #333;
                 span{
                     padding-left: 30px;
                     background: url("data:image/png;base64,") no-repeat;
                     background-size: 15px 15px;
                     background-position: 7px 50%;
                     &.active{
                          background: url("data:image/png;base64,") no-repeat;
                          background-size: 15px 15px;
                          background-position: 7px 50%;
                     }
                 }
             }
             .btn{
                 position: fixed;
                 bottom:30px;
                 left: 50%;
                 text-align: center;
                 margin-left: -173px;
                 width: 346px;
                 height:48px;
                 border-radius: 3px;
                 line-height: 48px;
                 font-size: 17px;
                 color:#fff;
                 background:#e03a3a ;
             }
         }
    }
</style>



