<template>
    <div>
        <!-- <div class="head">
            <div class="backBtn"></div>
            <div class="title">收货地址</div>
            <div class="status"></div>
        </div> -->
        <div class="content" v-if="list!=''&&emptyStatus">
            <div class="addressItem" v-for="(item,index) in list" @click="chooseAdd(item.id)">
                <div class="Info">
                    <div class="firstRow">
                        <span>{{item.realname}}</span>
                        <span>{{item.mobile}}</span>
                    </div>
                    <div class="secRow">
                        {{item.province}}{{item.city}}{{item.area}}{{item.address}}
                    </div>
                </div>
                <div class="handle">
                    <div class="defaultBtn" @click.stop="defaults(item.id)"><span :class="{change:item.isdefault==1,defaultIcon:true}" ></span>设为默认</div>
                    <div class="rightBtn">
                        <div class="editBtn" @click.stop="change(item.id)"><span class="editIcon" ></span>编辑</div>
                        <div class="delBtn" @click.stop="del(item.id)"><span class="delIcon" ></span>删除</div>
                    </div>
                </div>
            </div>
        </div>
        <div class="empty" v-if="list==''&&emptyStatus">
            <img src="https://pic.repaiapp.com/static/png/20180613/10/1528858078356110154.png">
        </div>
        <div class="addBtn" @click="handleAddAddress">新增地址</div>
        
    </div>
</template>

<script>
    import api from '../network/request'
    import { Toast } from 'vant'
    export default {
        name: "addresss",
        components: {

        },
        data(){
            return {
                "list":'',
                emptyStatus:false,
            }
        },
        methods: {
            //跳转商品详情页
            // particulars: function (id) {
            //     // console.log(id)
            //     this.$router.push({name: 'particulars', params: {'id': id}})
            // },
            chooseAdd:function(id){
                var _this=this;
                if(_this.type==1){
                    localStorage.setItem('chooseAddId',id);
                    setTimeout(function(){
                        _this.$router.go(-1);
                    },200)
                }
                
            },
            //获取用户地址信息
            getAddressData() {
                const access_token = localStorage.getItem("access_token");
                const params={access_token:access_token};
                var _this = this;
                api.post('user/address', params, res => {
                    if (res.status == 0) {
                        console.log(res);
                        _this.list = res.data.lists;
                        _this.emptyStatus=true;
                    }
                })
            },
            //删除地址
            del:function(id){
                const access_token = localStorage.getItem("access_token");
                const params={access_token:access_token,id:id};
                var _this = this;
                api.post('user/del_address', params, res => {
                    if (res.status == 0) {
                        console.log(res);
                        Toast.success("删除成功")
                        // $(".sj-hint").html("删除成功").fadeIn().delay(1500).fadeOut();
                        _this.getAddressData();
                        // _this.list = res.data.lists;
                    }
                }) 
            },
            //设置默认
            defaults:function(id){
                const access_token = localStorage.getItem("access_token");
                const params={access_token:access_token,id:id};
                var _this = this;
                api.post('user/default_address', params, res => {
                    if (res.status == 0) {
                        console.log(res);
                        Toast.success("设置成功")
                        // $(".sj-hint").html("设置成功").fadeIn().delay(1500).fadeOut();
                        if(_this.type==1){
                            setTimeout(function(){
                            _this.$router.go(-1);
                          },800)
                        }
                        _this.getAddressData();

                        // _this.list = res.data.lists;
                    }
                })
            },
            //重新编辑地址
            change:function(id){
                var _this=this;
                var addInfo='';
                for(var i in _this.list){
                    if(id==_this.list[i].id){
                        addInfo=_this.list[i];
                        break;
                    }
                }
               this.$router.push({name: 'addAddress',params:{addInfo:addInfo}})
            },
            //跳转到新增地址页面
            handleAddAddress() {
                this.$router.push({name: 'addAddress', params: {addInfo:''}})
            }
        },
        created() {
            var obj=this.$route.params;
            var _this=this;
            if(obj.hasOwnProperty("type")){
                _this.type=this.$route.params.type
            }
            
            this.getAddressData();
        }
    }
</script>

<style scoped lang="scss">
    .empty{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 14px;
        color: #999;
        padding-top: 100px;
    }
    .empty img{
        position: relative;
        width: 129px;
        height: 127px;
        margin-bottom: 10px;
    }
    .head {
        height: 45px;
        line-height: 45px;
        background-color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        color: #000000;
        border-bottom: 1px #dadada solid;
        .backBtn {
            flex-basis: 45px;
            box-sizing: border-box;
            width: 10px;
            height: 16px;
            background: url("data:image/png;base64,") no-repeat;
            background-size: contain;
            background-position: 10px;
        }
        .title {
            flex: 1;
            text-align: center;
        }
        .status {
            flex-basis: 45px;
            color: #dd3232;
            font-size: 15px;
        }
    }
    .content {
        .addressItem{
            margin-bottom:10px ;
            background: #fff;
            .Info{
                padding: 13px 15px 12px 15px;
                border-bottom:1px solid #dadada;
                .firstRow{
                    overflow: hidden;
                    font-size: 15px;
                    color:#000;
                    margin-bottom:2px;
                    span:first-child{
                        float: left;
                    }
                    span:last-child{
                        float: right;
                    }
                }
                .secRow{
                    font-size: 13px;
                    color:#2a2a2a;
                }
            }
            .handle{
                padding:14px 16px;
                font-size: 14px;
                color:#000;
                overflow:hidden;
                .defaultBtn{
                    float:left;
                    .defaultIcon{
                        display: inline-block;
                        vertical-align: middle;
                        width: 14px;
                        height: 14px;
                        margin-top: -2px;
                        margin-right: 3px;
                        background: url("data:image/png;base64,") no-repeat;
                        background-size: contain;
                    }
                    .change{
                        background: url("data:image/png;base64,") no-repeat;
                        background-size: contain;
                    }
                }

                .rightBtn{
                    float: right;
                    div{
                        float: left;
                        margin-left:10px ;
                    }
                    .editIcon{
                        display: inline-block;
                        vertical-align: middle;
                        width: 14px;
                        height: 14px;
                        margin-top: -2px;
                        margin-right: 3px;
                        background: url("data:image/png;base64,") no-repeat;
                        background-size: contain;
                    }
                    .delIcon{
                        display: inline-block;
                        vertical-align: middle;
                        width: 14px;
                        height: 14px;
                        margin-top: -2px;
                        margin-right: 3px;
                        background: url("data:image/png;base64,") no-repeat;
                        background-size: contain;
                    }
                }
            }
        }
    }
    .addBtn{
        position: fixed;
        bottom:30px;
        left: 50%;
        text-align: center;
        margin-left: -173px;
        width: 346px;
        height:48px;
        border-radius: 3px;
        line-height: 48px;
        font-size: 17px;
        color:#fff;
        background:#d9372d ;
    }

</style>



