<template>
    <div class="refund">
        <!-- <div class="head">
            <div class="backBtn"></div>
            <div class="title">申请退款</div>
            <div class="status"></div>
        </div> -->
        <div class="content">
            <div class="overview">
                <img :src="goods.thumb">
                <div class="textList">
                    <p class="title">{{goods.title}}</p>
                    <p class="orderNum">订单号：{{goods.ordersn}}</p>
                    <p class="price">售价：¥{{goods.price}}</p>
                    <p class="pay">实付：¥{{goods.realprice}}</p>
                </div>
            </div>
            <div class="inputItem">
                <p class="title">
                    <span class="redDot">*</span>商品状态
                </p>
                <div class="inputContent">
                    <div class="delivery">
                        <div :class="{active:deliveryState}" @click="handleSelect">已收到商品</div>
                        <div :class="{active:!deliveryState}" @click="handleSelect">未收到商品</div>
                    </div>
                </div>
            </div>
            <div class="inputItem">
                <p class="title">
                    <span class="redDot">*</span>您的个人信息
                </p>
                <div class="inputContent">
                    <div class="phone"><span>手机号</span><input placeholder="请输入您的手机号" maxlength="11" ref="input1"></input></div>
                </div>
            </div>
            <div class="inputItem">
                <p class="title">
                    <span class="redDot">*</span>描述详细原因
                </p>
                <div class="inputContent">
                    <div class="reason">
                        <textarea rows="5" maxlength="125" placeholder="请描述你遇到的问题，最多125个字符" ref="input2"></textarea>
                    </div>
                </div>
            </div>
            <div class="inputItem">
                <p class="title">
                    上传图片<span class="subTitle">(可选，最多可上传3张)</span>
                </p>
                <upload class="upload" size="small" @imgs="imgs" @delItem="delItem"></upload>
            </div>
            <div class="btn" @click="comfirm()">提交申请</div>
        </div>
        <div class="sj-hint"></div>    
    </div>
</template>

<script>
    import upload from 'components/upload/upload';
    import api from '../network/request'
    import { Toast } from 'vant'

    export default {
        name: "refund",
        data(){
            return {
                // "goodInfo":{
                //     "img":"https://pic.repaiapp.com/static/png/20180412/16/1523520607370764956.png",
                //     "title":"香天下手提火锅一盒",
                //     "orderNum":"201801123456",
                //     "price":28.8,
                //     "pay":28.8
                // },
                "picArr":[
                    "https://pic.repaiapp.com/static/png/20180417/11/1523935740194810257.png",
                    "https://pic.repaiapp.com/static/jpg/20180413/11/1523589580121399101.jpg",
                    "https://pic.repaiapp.com/static/png/20180412/16/1523520607370764956.png"
                ],
                "deliveryState": true,
                orderid:'',
                goodsid:'',
                goods:'',
                imgss:''
            }
        },
        components:{
            upload,
            // imgs,
        },
        methods: {
            imgs:function(e){
                console.log(e);
                this.imgs=e;
                this.imgss=e;
            },
            delItem(index) {
                this.imgs.splice(index,1)
            },
            handleSelect() {
                if(this.deliveryState){
                    this.deliveryState = false
                } else {
                    this.deliveryState = true
                }
            },
            shopInfo:function(){
                var _this=this;
                const params={access_token:localStorage.getItem('access_token'),orderid:_this.orderid,goodsid:_this.goodsid};
                api.post('refund/info',params,res=>{
                console.log(res);
                    if(res.status==0){
                        _this.goods=res.data.goods
                    }
                    if(res.status==600){
                      this.$router.push({name: 'login', params: {}})
                    }
                })
            },
            comfirm:function(){
                 var _this=this;
                if(this.imgs.length>0&&this.imgss!=''){
                    var usrimg=this.imgs.join(',');
                }else{
                    var usrimg=this.imgss;
                }
               
                var re = /^1[3|4|5|7|8][0-9]\d{4,8}$/;
                if(this.deliveryState){
                    var rtype=1;
                }else{
                    var rtype=0;
                }
                var phone=this.$refs.input1.value;
                var content=this.$refs.input2.value
                if(phone==''){
                    $(".sj-hint").html("请输入手机号").fadeIn().delay(1500).fadeOut();
                    return;
                }
                if(!re.test(phone)){
                  $(".sj-hint").html("手机号格式有误").fadeIn().delay(1500).fadeOut();
                  return;
                }
                if(content==''){
                    $(".sj-hint").html("请输入退款原因").fadeIn().delay(1500).fadeOut();
                    return;
                }

                const params={access_token:localStorage.getItem("access_token"),orderid:_this.orderid,goodsid:_this.goodsid,rtype:rtype,phone:phone,content:content,img:usrimg};
                api.post('refund/application',params,res=>{
                console.log(res);
                    if(res.status==0){
                        Toast.success("申请成功");
                        this.goback()
                       // this.$router.go(-1);
                    }
                    if(res.status==600){
                      this.$router.push({name: 'login', params: {}})
                    }
                })
                // $(".sj-hint").html("确认成功").fadeIn().delay(1500).fadeOut();

            },
            goback:function(){
                var _this=this;
                setTimeout(function(){
                    _this.$router.go(-1);
                 },1800)
            }
        },
        created() {
            this.orderid=this.$route.params.orderid;
            this.goodsid=this.$route.params.goodsid;
            this.shopInfo();
        },
        
    }
</script>

<style scoped lang="scss">
    .refund{
        .head{
            height: 45px;
            line-height: 45px;
            background-color: #fff;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 18px;
            color: #000000;
            border-bottom: 1px #dadada solid;
            .backBtn{
                flex-basis: 45px;
                box-sizing: border-box;
                width: 10px;
                height: 16px;
                background: url("data:image/png;base64,") no-repeat;
                background-size: contain;
                background-position:10px;
            }
            .title{
                flex: 1;
                text-align: center;
            }
            .status{
                flex-basis: 45px;
                color: #dd3232;
                font-size: 15px;
            }
        }
        .content {
            .overview {
                display: flex;
                padding: 15px;
                background: #fff;
                img{
                    flex-basis: 72px;
                    width: 72px;
                    height: 72px;
                    margin-right: 15px;
                }
                .textList{
                    flex: 1;
                    .title{
                        font-size: 14px;
                        color:#343032;
                    }
                    .orderNum{
                        font-size: 12px;
                        color: #999;
                    }
                    .price{
                        font-size: 12px;
                        color: #999;
                    }
                    .pay{
                        font-size: 12px;
                        color: #ec1a1d;
                    }
                }
            }
            .inputItem{
                .title{
                    padding-left: 13px;
                    height: 37px;
                    line-height: 37px;
                    font-size: 14px;
                    color: #999;
                    background:#f6f6f6;
                    .redDot{
                        margin-right:2px ;
                        color: #e42d2d;
                    }
                    .subTitle{
                        font-size: 12px;
                    }
                }
                .upload{
                    margin-left: 10px;
                }
                .inputContent{
                    font-size: 14px;
                    color:#333;
                    background: #fff;
                    .delivery{
                        padding-left:10px;
                        div{
                            padding:13px 30px;
                            background: url("data:image/png;base64,") no-repeat;
                            background-size: 15px 15px;
                            background-position: 7px 50%;
                            &:last-child{
                                border-top:1px solid #ddd;
                            }
                            &.active{
                                  background: url("data:image/png;base64,") no-repeat;
                                  background-size: 15px 15px;
                                  background-position: 7px 50%;
                            }
                        }
                    }
                    .phone{
                        height:60px;
                        line-height:60px;
                        span{
                            padding-left: 13px;
                        }
                        input{
                            margin-left:20px;
                            border:0;
                            outline: none;
                            height: 100%;
                        }
                    }
                    .reason{
                        textarea{
                            box-sizing: border-box;
                            resize:none;
                            outline:none;
                            width: 100%;
                            padding: 2%;
                            border:0;
                        }
                    }
                }
            }
            .btn{
                text-align: center;
                margin: 20px auto;
                width: 346px;
                height:48px;
                border-radius: 3px;
                line-height: 48px;
                font-size: 17px;
                color:#fff;
                background:#e03a3a ;
            }
        }
    }

</style>



