<template>
    <div class="photos">
        <div class="head">
            <div class="backBtn"></div>
            <div class="title">香天下创意图集</div>
            <div class="status"></div>
        </div>
        <div class="content">
            <ul class="photosContent">
                <li class="photoItem" v-for="(item,index) in picArr">
                    <img :src="item">
                </li>
            </ul>
            <div class="overview">
                <div class="title">香天下创意图集<span>(505人浏览)</span></div>
                <div class="assess">
                    <div class="assessLeft"><span class="iconShop"></span>香天下手提火锅</div>
                    <div class="assessRight">
                        <div><span class="iconInfo"></span>0</div>
                        <div><span class="iconGood"></span>58</div>
                    </div>
                </div>
            </div>
            <div class="reviewDisplay">
                <div class="title"></div>
                <p class="tips">暂时还没有评论  快来抢沙发吧</p>
            </div>
        </div>
        <div class="footer">
            <div class="input">
                <span class="iconInput"></span>
                <input type="text" placeholder="写写你的看法吧">
            </div>
            <div class="icon">
                <span class="iconInfo"></span>
                <span class="iconGood"></span>
                <span class="iconShare"></span>
            </div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">

    export default {
        name: "photos",
        data(){
            return {
                "picArr":[
                    "https://pic.repaiapp.com/static/png/20180417/11/1523935740194810257.png",
                    "https://pic.repaiapp.com/static/jpg/20180413/11/1523589580121399101.jpg",
                    "https://pic.repaiapp.com/static/png/20180412/16/1523520607370764956.png",
                    "https://pic.repaiapp.com/static/png/20180417/11/1523935740194810257.png",
                    "https://pic.repaiapp.com/static/jpg/20180413/11/1523589580121399101.jpg",
                    "https://pic.repaiapp.com/static/png/20180412/16/1523520607370764956.png",
                    "https://pic.repaiapp.com/static/png/20180417/11/1523935740194810257.png",
                    "https://pic.repaiapp.com/static/jpg/20180413/11/1523589580121399101.jpg",
                    "https://pic.repaiapp.com/static/png/20180412/16/1523520607370764956.png"
                ]
            }
        },
        methods: {

        },
        created() {

        },
        components:{
        }
    }
</script>

<style scoped lang="scss">
    .photos{
        .head{
            height: 45px;
            line-height: 45px;
            background-color: #fff;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 18px;
            color: #000000;
            border-bottom: 1px #dadada solid;
            .backBtn{
                flex-basis: 45px;
                box-sizing: border-box;
                width: 10px;
                height: 16px;
                background: url("data:image/png;base64,") no-repeat;
                background-size: contain;
                background-position:10px;
            }
            .title{
                flex: 1;
                text-align: center;
            }
            .status{
                flex-basis: 45px;
                color: #dd3232;
                font-size: 15px;
            }
        }
        .content {
            .photosContent{
                overflow: hidden;
                padding:6px;
                background:#fff;
                .photoItem{
                    list-style: none;
                    float: left;
                    width: 31.3%;
                    height: 118px;
                    margin: 1%;
                    img{
                        width: 100%;
                        height: 100%;
                    }
                }
            }
            .overview{
                padding:9px;
                background:#fff;
                .title{
                    margin-bottom: 5px;
                    font-size: 15px;
                    color: #000;
                    span{
                        font-size: 12px;
                        color: #a7a7a7;
                    }
                }
                .assess{
                    overflow: hidden;
                    font-size:12px;
                    color:#a7a7a7;
                    height:22px;
                    line-height:22px;
                    .assessLeft{
                        float: left;
                        .iconShop{
                            display: inline-block;
                            vertical-align: middle;
                            width: 22px;
                            height: 22px;
                            margin-right:8px;
                            background: url("data:image/png;base64,") no-repeat;
                            background-size: contain;
                        }
                    }
                    .assessRight{
                        float: right;
                        div{
                            display: inline-block;
                            vertical-align: middle;
                            margin: 0 10px;
                            font-size:13px;
                            span{
                                display: inline-block;
                                vertical-align: middle;
                                margin-top:-3px;
                                margin-right:5px;
                            }
                            .iconInfo{
                                width: 18px;
                                height: 16px;
                                background: url("data:image/png;base64,") no-repeat;
                                background-size: contain;
                            }
                            .iconGood{
                                width: 16px;
                                height: 16px;
                                background: url("data:image/png;base64,") no-repeat;
                                background-size: contain;
                            }
                        }
                    }
                }
            }
            .reviewDisplay{
                text-align: center;
                margin-top: 10px;
                background:#fff;
                padding: 20px 10px;
                .title{
                    width: 55px;
                    height: 15px;
                    margin: auto;
                    background: url("data:image/png;base64,") no-repeat;
                    background-size: contain;
                }
                .tips{
                    margin: 30px 0;
                    font-size: 14px;
                    color: #a7a7a7;
                }
            }
        }
        .footer{
            position: fixed;
            display: flex;
            z-index: 10;
            bottom: 0;
            width: 100%;
            padding: 10px;
            background-color: #fff;
            box-shadow: 0px 0px 44px 0px rgba(0, 0, 0, 0.23);
            div{
                flex:1;
                align-items: center;
            }
            .input{
                display: flex;
                padding:6px;
                border:1px solid #e5e5e5;
                border-radius: 15px;
                background:#f5f5f5;
                .iconInput{
                    width: 17px;
                    height: 17px;
                    margin: 0 7px;
                    background: url("data:image/png;base64,") no-repeat;
                    background-size: contain;
                }
                input{
                    flex:1;
                    border: none;
                    outline: 0;
                    resize: none;
                    background:#f5f5f5;
                }

            }
            .icon{
                flex:1;
                display: flex;
                span{
                    flex:1;
                    text-align: center;
                    width: 16px;
                    height: 16px;
                }
                .iconInfo{
                    background: url("data:image/png;base64,") no-repeat;
                    background-size: contain;
                    background-position: center;
                }
                .iconGood{
                    background: url("data:image/png;base64,") no-repeat;
                    background-size: contain;
                    background-position: center;
                }
                .iconShare{
                    background: url("data:image/png;base64,") no-repeat;
                    background-size: contain;
                    background-position: center;
                }
            }
        }
    }
</style>



