<template>
    <div class="evaluationList">
        <!-- <div class="head">
            <div class="backBtn"></div>
            <div class="title">评价列表</div>
            <div class="status"></div>
        </div> -->
        <div class="content">
            <div class="goodContent" v-for="(item,index) in goods">
                <img class="goodImg" :src='item.thumb'/>
                <div class="goodText">
                    <div class="goodTitle">{{item.title}}</div>
                </div>
                <!-- <div class="btn">查看详情</div> -->
                <div class="btn" v-if="item.status==0" @click="gotoreview(item.goodsid,item.thumb)">晒单评论</div>
                <div class="btn" v-else @click="reviewDetail(item.goodsid)">查看详情</div>
            </div>
        </div>
    </div>
</template>

<script>
    import api from '../network/request'

    export default {
        name: "reviewList",
        components: {

        },
        data(){
            return {
                "goods":[
                    {
                        "img":"https://pic.repaiapp.com/static/png/20180412/16/1523520607370764956.png",
                        "title":"香天下手提火锅一盒"
                    },
                    {
                        "img":"https://pic.repaiapp.com/static/png/20180412/16/1523520607370764956.png",
                        "title":"香天下手提火锅一盒"
                    }
                ]
            }
        },
        methods: {
            list:function(){
                var _this=this;
                const params={access_token:localStorage.getItem('access_token'),orderid:localStorage.getItem('myorderid')};
                api.post('order/com_goods',params,res=>{
                console.log(res);
                    if(res.status==0){
                        _this.goods=res.data
                    }
                    if(res.status==600){
                      this.$router.push({name: 'login', params: {}})
                    }
                })
            },
            gotoreview:function(goodsid,img){
                this.$router.push({name: 'goodReview', params: {goodsid:goodsid,img:img}})
            },
            reviewDetail:function(goodsid){
                this.$router.push({name: 'reviewDetail', params: {goodsid:goodsid}})
            }

        },
        created() {
            this.list();

        }
    }
</script>

<style scoped lang="scss" type="text/scss">
    .evaluationList {
        position: fixed;
        top:0;
        bottom:0;
        left:0;
        right:0;
        background:#fff;
        .head {
            height: 45px;
            line-height: 45px;
            background-color: #fff;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 18px;
            color: #000000;
            border-bottom: 1px #dadada solid;
            .backBtn {
                flex-basis: 45px;
                box-sizing: border-box;
                width: 10px;
                height: 16px;
                background: url("data:image/png;base64,") no-repeat;
                background-size: contain;
                background-position: 10px;
            }
            .title {
                flex: 1;
                text-align: center;
            }
            .status {
                flex-basis: 45px;
                color: #dd3232;
                font-size: 15px;
            }
        }
        .content {
            .goodContent {
                display: flex;
                align-items: center;
                padding: 17px;
                border-bottom: 1px solid #dadada;
                img {
                    box-sizing: border-box;
                    flex-basis: 80px;
                    width: 80px;
                    height: 80px;
                    border: 1px solid #bec2c9;
                }
                .goodText {
                    flex: 1;
                    align-self: flex-start;
                    padding-left: 16px;
                    font-size: 14px;
                    line-height: 18px;
                    color: #000;
                }
                .btn{
                    align-self: flex-end;
                    padding:5px 8px;
                    font-size:14px;
                    color: #333;
                    border:1px solid #999;
                    border-radius:2px;
                    /*&:hover{
                        color:#ef4242;
                        border:1px solid #ef4242;
                    }*/
                }
            }
        }
    }
</style>



